.popup__container {
  max-width: 430px;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 36px 36px 37px;
  position: relative;
  box-sizing: border-box;
}

@media screen and (max-width: 510px) {
  .popup__container {
    margin: 40.7px;
  }
}
